import React, { useEffect, useState } from 'react'
import Box from '@mui/material/Box'
import { TextField, InputAdornment } from '@mui/material'
import SearchIcon from '@mui/icons-material/Search'
import { useNavigate } from 'react-router-dom'

const MainPage = (props) => {
  const [userId, setUserId] = useState('')
  const navigate = useNavigate()

  const add = async () => {
    try {
      await fetch('https://solved.ac/api/v3/user/show?handle=' + userId)
        .then((res) => res.json())
        .then((data) => {
          navigate('/userPage', { state: { userId } })
          props.setGlobalID(userId)
        })
    } catch (error) {
      alert(' ID Solve.ac  .')
    }
  }
  // useEffect(() =>
  // // console.log(props)
  // , []);
  return (
    <div className="mainPage">
      {/* box   textfield  box     
            textfield padding   = 2.9,  = 2  
             searchIcon     */}
      <Box
        sx={{
          // backgroundColor: "#F2F2F2",
          borderRadius: 25,
          width: '60%',
          textAlign: 'center',
          boxShadow: '2px 8px 20px -12px #bdbdbd',
        }}
      >
        <TextField
          onKeyPress={(e) => {
            if (e.key === 'Enter') {
              add()
            }
          }}
          variant="standard"
          id="User_ID"
          placeholder="Solved.ac ID "
          sx={{
            width: '90%',
            px: 2.9,
            py: 2,
          }}
          value={userId}
          onChange={(e) => setUserId(e.target.value)}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                {/* <Link to="/userPage" state={{ userId }}> */}
                <SearchIcon onClick={add} />
                {/* </Link> */}
              </InputAdornment>
            ),
          }}
        />
      </Box>
    </div>
  )
}

export default MainPage
