import React, { useState, useEffect } from 'react'
import Box from '@mui/material/Box'
import { TextField } from '@mui/material'
import './register.css'
import Fade from '@mui/material/Fade'
import { useNavigate } from 'react-router-dom'
import { useLocation } from 'react-router-dom'

const StudentRegister = (e) => {
  const navigate = useNavigate()

  const [studentId, setStudentId] = useState('')
  const [studentName, setStudentName] = useState('')
  const [studentCode, setStudentCode] = useState('')
  const [bojId, setBojId] = useState('')
  const location = useLocation()
  const lecID = location.state[0].dataID

  const [checked, setChecked] = React.useState(false)

  const onClickSubmit = async (props) => {
    try {
      // console.log(props)
      if (props.studentId === '') return alert(' ')
      else if (props.studentName === '') return alert('  ')
      else if (props.studentCode === '') return alert('  ')
      else if (props.bojId === '') return alert(' ID ')
      //   JSON       
      const sbody = {
        sI: props.studentId,
        sN: props.studentName,
        sC: props.studentCode,
        bI: props.bojId,
        lI: props.lecID,
      }
      const requestOptions = {
        //      ,  .
        method: 'POST', // POST     , GET   . PUT , DELETE 
        headers: {
          'Content-Type': 'application/json',
        }, // json   .
        body: JSON.stringify(
          //  body     .
          sbody
        ),
      }
      //  URL exprees   3000    ,   
      await fetch(e.serverAddress + '/studentRegister', requestOptions)
        .then((res) => res.json()) // res   PROMISE   
        .then((data) => {
          // .then       JSON     
          //        
          if (data === '  .')
            return alert('  .')
          else if (data === ' .   .')
            return alert(' .   .')
          if (!alert(data)) navigate('/assignments')
        })
    } catch (error) {
      console.error(error)
    }
  }

  useEffect(() => {
    setChecked(true)
  }, [])

  return (
    <div className="registerPage">
      <Fade
        in={checked}
        style={{ transformOrigin: '0 0 0' }}
        {...(checked ? { timeout: 1000 } : {})}
      >
        <div className="regiBox">
          <h2 style={{ margin: '0%', textAlign: 'center' }}> </h2>
          <h6
            style={{ margin: '5% 0%', textAlign: 'center', color: '#5D5D5D' }}
          >
            {location.state[1].lectureName}
             . , , Baekjoon  .
          </h6>
          {/* box   textfield  box     
            textfield padding   = 2.9,  = 2  
             searchIcon     */}
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="STUDENT_ID"
              placeholder=""
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={studentId}
              onChange={(e) => setStudentId(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="STUDENT_NAME"
              placeholder=""
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={studentName}
              onChange={(e) => setStudentName(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="STUDENT_CODE"
              placeholder="*STUDENT CODE"
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={studentCode}
              onChange={(e) => setStudentCode(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="BOJ_ID"
              placeholder="(Baekjoon) ID"
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={bojId}
              onChange={(e) => setBojId(e.target.value)}
            />
          </Box>
          <button
            className="submitButton"
            onClick={() =>
              onClickSubmit({
                studentId,
                studentName,
                studentCode,
                bojId,
                lecID,
              })
            }
          >
            
          </button>
        </div>
      </Fade>
    </div>
  )
}

export default StudentRegister
