import React, { useState } from 'react'

import Logo from '../../image/logo.png'

import Dialog from '@mui/material/Dialog'
import DialogContent from '@mui/material/DialogContent'
import Slide from '@mui/material/Slide'
import { VerticalAlignCenter } from '@mui/icons-material'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})
const Create = (e) => {
  //    
  const [open, setOpen] = useState(false)
  const userHandleOpen = () => setOpen(!open)

  //   name password
  const [createUserName, setCreateUserName] = useState('')
  const [createUserPassword, setCreateUserPassword] = useState('')

  //     createUser 
  const createUser = async (props) => {
    try {
      //   body 
      const body = {
        name: props.createUserName,
        password: props.createUserPassword,
      }

      const requestOptions = {
        //      ,  .
        method: 'POST', // POST     , GET   . PUT , DELETE 
        headers: {
          'Content-Type': 'application/json',
        }, // json   .
        body: JSON.stringify(body),
      }
      await fetch(e.serverAddress + '/QnAUser', requestOptions)
        .then((res) => res.json()) // res   PROMISE   
        .then((data) => {
          // .then       JSON     
          if (data.error) {
            if (data.error === 1062) alert('  .')
          } else {
            alert(data.data)
            userHandleOpen()
          }
        })
    } catch (error) {
      //   
      alert('2.')
      console.error(error)
    }
  }

  return (
    <div className="comments_create">
      <button className="comment_button" onClick={userHandleOpen}>
        
      </button>
      <Dialog
        open={open}
        onClose={userHandleOpen}
        TransitionComponent={Transition}
        keepMounted
      >
        <DialogContent sx={{ width: '500%' }}>
          <img
            src={Logo}
            style={{ marginTop: '1%', width: '3rem' }}
            alt="profile"
          />
          {/* <spna>Sign Up</spna> */}
          <input
            className="input_name"
            onChange={(e) => setCreateUserName(e.target.value)}
            placeholder="Name"
            type="text"
            value={createUserName}
          />
          <input
            className="input_password"
            onChange={(e) => setCreateUserPassword(e.target.value)}
            placeholder="password"
            type="password"
            value={createUserPassword}
          />
          <button
            className="input_button"
            disabled={!createUserName || !createUserPassword}
            name="commenting"
            value="Signup"
            onClick={() => {
              createUser({ createUserName, createUserPassword })
              // createUserName createUserPassword  
              setCreateUserName('')
              setCreateUserPassword('')
            }}
          >
            Signup
          </button>
        </DialogContent>
      </Dialog>
    </div>
  )
}

export default Create
