import React, { useState, useEffect } from 'react'
import Box from '@mui/material/Box'
import { TextField } from '@mui/material'
import './register.css'
import Fade from '@mui/material/Fade'
import { useNavigate } from 'react-router-dom'

const Register = (e) => {
  const [userId, setUserId] = useState('')
  const [regiCode, setRegiCode] = useState('')
  const [gitId, setGitId] = useState('')

  const [checked, setChecked] = React.useState(false)
  const navigate = useNavigate()
  const onClickSubmit = async (props) => {
    try {
      // console.log(props)
      if (props.userId === '') return alert(' ID ')
      else if (props.regiCode === '') return alert('REGISTER CODE ')
      //   JSON       
      const sbody = {
        uI: props.userId,
        rC: props.regiCode,
        gI: props.gitId,
      }
      const requestOptions = {
        //      ,  .
        method: 'POST', // POST     , GET   . PUT , DELETE 
        headers: {
          'Content-Type': 'application/json',
        }, // json   .
        body: JSON.stringify(
          //  body     .
          sbody
        ),
      }
      //  URL exprees   3000    ,   
      await fetch(e.serverAddress + '/register', requestOptions)
        .then((res) => res.json()) // res   PROMISE   
        .then((data) => {
          if (data === '  .')
            return alert(data)
            else if (data === ' .   .')
            return alert(data)
            else if (data === 'Solved.ac  ID      ')
            return alert(data)
            else if (data === '  .   ')
            return alert(data)
            else if(data==='  .    .') 
            navigate('/rank')
            return alert(data);
          // if (!alert(data)) navigate('/rank')
        })
    } catch (error) {
      console.error(error)
    }
  }

  useEffect(() => {
    setChecked(true)
  }, [])

  return (
    <div className="registerPage">
      <Fade
        in={checked}
        style={{ transformOrigin: '0 0 0' }}
        {...(checked ? { timeout: 1000 } : {})}
      >
        <div className="regiBox">
          <h2 style={{ margin: '0%', textAlign: 'center' }}></h2>
          <h6
            style={{ margin: '5% 0%', textAlign: 'center', color: '#5D5D5D' }}
          >
            &lt;&gt;     .
          </h6>
          {/* box   textfield  box     
            textfield padding   = 2.9,  = 2  
             searchIcon     */}
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="User_ID"
              placeholder="Baekjoon ID"
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={userId}
              onChange={(e) => setUserId(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="Register_CODE"
              placeholder="REGISTER CODE"
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={regiCode}
              onChange={(e) => setRegiCode(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="Git_ID"
              placeholder="* : Github ID"
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={gitId}
              onChange={(e) => setGitId(e.target.value)}
            />
          </Box>
          <button
            className="submitButton"
            onClick={() => {
              // console.log("@@@@@@@@@@@@@@");
              onClickSubmit({ userId, regiCode, gitId })
            }}
          >
            
          </button>
        </div>
      </Fade>
    </div>
  )
}

export default Register
