import React, { useState, useEffect } from 'react'
import Box from '@mui/material/Box'
import { TextField } from '@mui/material'
import './register.css'
import Fade from '@mui/material/Fade'
import ControlledRadioButtonsGroup from './MUI/ControlledRadioButtonsGroup'
import { useNavigate } from 'react-router-dom'

const ProRegister = (e) => {
  const navigate = useNavigate()

  const [proName, setProName] = useState('')
  const [proCode, setProRegiCode] = useState('')
  const [subCode, setSubCode] = useState('')
  const [subName, setSubName] = useState('')
  const [classNum, setClassNum] = useState('')

  const [checked, setChecked] = React.useState(false)

  const onClickSubmit = async (props) => {
    try {
      // console.log(props)
      if (props.proName === '') return alert('  ')
      else if (props.proCode === '') return alert('  ')
      else if (props.subCode === '') return alert('  ')
      else if (props.subName === '') return alert(' ')
      else if (props.classNum === '') return alert('  ')
      //   JSON       
      const sbody = {
        pN: props.proName,
        pC: props.proCode,
        sC: props.subCode,
        sN: props.subName,
        cN: props.classNum,
      }
      const requestOptions = {
        //      ,  .
        method: 'POST', // POST     , GET   . PUT , DELETE 
        headers: {
          'Content-Type': 'application/json',
        }, // json   .
        body: JSON.stringify(
          //  body     .
          sbody
        ),
      }
      //  URL exprees   3000    ,   
      await fetch(e.serverAddress + '/proRegister', requestOptions)
        .then((res) => res.json()) // res   PROMISE   
        .then((data) => {
          // .then       JSON     
          //        
          if (data === '  .')
            return alert('  .')
          if (!alert(data)) navigate('/assignments')
        })
    } catch (error) {
      console.error(error)
    }
  }

  useEffect(() => {
    setChecked(true)
  }, [])

  return (
    <div className="registerPage">
      <Fade
        in={checked}
        style={{ transformOrigin: '0 0 0' }}
        {...(checked ? { timeout: 1000 } : {})}
      >
        <div className="regiBox">
          <h2 style={{ margin: '0%', textAlign: 'center' }}> </h2>
          <h6
            style={{ margin: '5% 0%', textAlign: 'center', color: '#5D5D5D' }}
          >
                  .
          </h6>
          {/* box   textfield  box     
            textfield padding   = 2.9,  = 2  
             searchIcon     */}
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="PRO_NAME"
              placeholder=" "
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={proName}
              onChange={(e) => setProName(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="PROFESSOR_CODE"
              placeholder="*PROFESSOR CODE"
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={proCode}
              onChange={(e) => setProRegiCode(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
              marginBottom: '5%',
            }}
          >
            <TextField
              variant="standard"
              id="SUB_CODE"
              placeholder=""
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={subCode}
              onChange={(e) => setSubCode(e.target.value)}
            />
          </Box>
          <Box
            sx={{
              backgroundColor: '#F2F2F2',
              borderRadius: 25,
              textAlign: 'center',
            }}
          >
            <TextField
              variant="standard"
              id="SUB_NAME"
              placeholder=""
              sx={{
                width: '90%',
                px: 2.9,
                py: 2,
              }}
              value={subName}
              onChange={(e) => setSubName(e.target.value)}
            />
          </Box>
          <h6 style={{ margin: '5% 0%' }}>
                  ?
          </h6>
          <div>
            <ControlledRadioButtonsGroup
              classNum={classNum}
              setClassNum={setClassNum}
            ></ControlledRadioButtonsGroup>
          </div>
          <button
            className="submitButton"
            onClick={(e) => {
              // if(e.key === 'Enter') {
              //   onClickSubmit({ proName, proCode, subCode, subName, classNum })
              // }
              onClickSubmit({ proName, proCode, subCode, subName, classNum })
            }}
          >
            
          </button>
        </div>
      </Fade>
    </div>
  )
}

export default ProRegister
